<template>
  <div class="form-group">
    <h2>{{ place }}</h2>
    <h2>{{ value }}</h2>
    <input :id="id" v-model="text" class="form-control" :placeholder="place">
    <label :for="id">asda</label>
  </div>
</template>

<script>
export default {
  name: 'StringInput',
  porps: {
    value: {
      type: String,
      require: true
    },
    place: {
      type: String,
      default: '占位信息'
    }
  },
  computed: {
    id () {
      var Num = 'input_'
      for (var i = 0; i < 6; i++) {
        Num += Math.floor(Math.random() * 10)
      }
      return Num
    },
    text: {
      get () {
        console.log(this.value)
        return this.value
      },
      set (value) {
        console.log(value)
        this.$emit('input', value)
      }
    }
  }
}
</script>

<style lang="less">
  .form-group{
    margin: 18px 0;
    position: relative;
    padding: 0 30px;
    >label{
      position: absolute;
      z-index: 11;
      line-height: 20px;
      font-size: 18px;
      font-weight: bold;
      top: 0;
      transition: 500ms;
    }
    input{
      position: absolute;
      z-index: 10;
      border: 1.5px solid;
      border-color: transparent transparent var(--main-background-color) transparent ;
      line-height: 20px;
      transition: 500ms;
      width: 100%;
    }
    input:hover{
      border-color:transparent transparent  var(--theme-color) transparent;
    }
    input:focus{
      +label{
        top: -18px;
        font-size: 14px;
      }
      border-color:transparent transparent  var(--theme-color) transparent;
      outline: 0;
    }
  }
</style>
